body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f4;
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

h1 {
  background-color: #4caf50;
  color: white;
  padding: 1rem;
}

.main-content {
  display: flex;
  justify-content: space-between;
}

.user-list {
  width: 200px;
  height: 400px;
  overflow-y: auto;
  border: 1px solid #ddd;
  padding: 1rem;
  margin-right: 2rem;
}

.user-list ul {
  list-style-type: none;
  padding: 0;
}

.user-list li {
  background-color: #fff;
  margin: 0.5rem 0;
  padding: 0.5rem;
  border: 1px solid #ddd;
  cursor: pointer;
}

.user-list li:hover {
  background-color: #e0e0e0;
}

.user-details {
  flex-grow: 1;
  padding: 1rem;
  border: 1px solid #ddd;
  background-color: #fff;
}

.user-details p {
  text-align: left;
  margin: 0.5rem 0;
}




